body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.board {
  border: tan solid 12px;
  border-top: #bda27e solid 12px;
  border-left: #b19876 solid 12px;
  border-bottom: #c9ad86 solid 12px;
  box-shadow: 0px 0px 6px 5px rgba(58, 18, 13, 0), 0px 0px 0px 2px #c2a782, 0px 0px 0px 4px #a58e6f, 3px 4px 8px 5px rgba(0, 0, 0, 0.5);
  border-radius: 8px;
  background: url(/static/media/wood.53cd660d.jpg);
  background-size: cover;
  position: relative; }
  .board .mask {
    width: 100%;
    height: 100%;
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    position: absolute;
    z-index: 1000; }
    .board .mask .finish_popup {
      background-color: rgba(0, 0, 0, 0.4);
      padding-top: 24px;
      margin: auto;
      width: 75%;
      height: 300px;
      border-radius: 10px;
      margin-top: 165px;
      color: #cacaca;
      font-size: 34px;
      font-weight: bolder; }
      .board .mask .finish_popup span {
        display: block;
        text-align: center; }
      .board .mask .finish_popup .tail {
        background-image: url(/static/media/t.457cc00d.png);
        background-repeat: no-repeat;
        background-size: 100% 100%;
        background-position: center center;
        height: 200px;
        width: 200px;
        margin: auto; }
      .board .mask .finish_popup .head {
        background-image: url(/static/media/h.6fece29b.png);
        background-repeat: no-repeat;
        background-size: 100% 100%;
        background-position: center center;
        height: 200px;
        width: 200px;
        margin: auto; }
      .board .mask .finish_popup .selection {
        text-align: center;
        width: 60%;
        background-color: rgba(57, 181, 71, 0.5);
        margin: 60px auto;
        cursor: pointer;
        line-height: 2.4rem;
        -webkit-user-select: none;
           -moz-user-select: none;
            -ms-user-select: none;
                user-select: none;
        border-radius: 10px; }
    .board .mask .finish_popup.tie {
      margin-top: 250px;
      height: 110px; }

.board-row:after {
  clear: both;
  content: "";
  display: table; }

.status {
  margin: 10px 0px;
  color: #ececec;
  font-family: serif; }

.coin_icon {
  height: 30px;
  width: 30px;
  display: inline-block;
  position: relative;
  top: 7px;
  left: 5px; }

.coin_icon.tail {
  background-image: url(/static/media/t.457cc00d.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center; }

.coin_icon.head {
  background-image: url(/static/media/h.6fece29b.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center; }

.square {
  color: #ececec;
  transition: background 0.4s;
  border: 2px solid rgba(121, 73, 73, 0.3);
  float: left;
  font-weight: bold;
  line-height: 34px;
  height: 80px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 80px;
  cursor: pointer;
  font-family: serif;
  background-color: transparent; }

.square.available {
  background: rgba(57, 181, 74, 0.36); }

.square:focus {
  outline: none; }

.square .inner {
  width: 100%;
  height: 100%;
  background-color: transparent; }

.square.head .inner {
  background-image: url(/static/media/h.6fece29b.png);
  background-repeat: no-repeat;
  background-size: 85% 85%;
  background-position: center center; }

.square.tail .inner {
  background-image: url(/static/media/t.457cc00d.png);
  background-repeat: no-repeat;
  background-size: 85% 85%;
  background-position: center center; }

.kbd-navigation .square:focus {
  background: #ddd; }

.game {
  display: flex;
  flex-direction: row;
  width: 900px;
  position: relative;
  margin: auto;
  margin-bottom: 20px; }

.game-info {
  margin-left: 20px; }

.clear {
  position: absolute;
  width: 200px;
  height: 40px;
  font-size: 18px;
  font-family: serif;
  font-weight: bolder;
  left: 448px;
  bottom: -36px;
  border-radius: 5px;
  background-color: #ececec;
  color: #231a09;
  cursor: pointer; }

.game .scores {
  /* background-color: white; */
  width: 200px;
  height: 600px;
  position: absolute;
  top: 68px;
  left: 680px;
  display: flex;
  color: #cacaca;
  font-family: serif;
  font-size: 42px; }

.game .scores .head {
  background-image: url(/static/media/h.6fece29b.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center;
  width: 80px;
  height: 80px;
  position: absolute; }

.game .scores .tail {
  background-image: url(/static/media/t.457cc00d.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center;
  width: 80px;
  height: 80px;
  position: absolute; }

.ant-message {
  top: 140px !important;
  left: -122px !important; }

.ant-spin.ant-spin-spinning {
  color: #ffffff;
  margin-left: 10px; }

.game-info {
  width: 900px;
  margin: auto;
  margin-top: 60px;
  margin-bottom: 60px; }

.mobile-status {
  display: none; }

@media (max-width: 768px) {
  .ant-message {
    top: 0px !important;
    left: 0px !important; }
  .main {
    padding-left: 4px !important;
    padding-right: 4px !important; }
    .main .status.white_font {
      font-size: 13px; }
  .game {
    width: 100%; }
    .game .game-board {
      width: 100%; }
    .game .clear {
      left: 10px;
      bottom: -54px; }
    .game .board {
      border: none;
      margin-left: .14em;
      margin-right: .14em; }
      .game .board .square {
        width: 1.2em;
        height: 1.2em; }
      .game .board .finish_popup {
        margin-top: 3.6em;
        height: 200px;
        font-size: .5em; }
        .game .board .finish_popup .selection {
          margin: 1.4em auto; }
        .game .board .finish_popup .head {
          height: 6em;
          width: 6em; }
      .game .board .finish_popup.tie {
        font-size: .5em; }
  .game .scores {
    display: none; }
  .game-info {
    width: auto;
    margin-top: 0px; }
  .mobile-status {
    text-align: center;
    display: block;
    margin-top: 60px; } }

.main {
  background-color: #2a2a2a;
  padding: 20px; }
  .main .white_font {
    color: #cacaca;
    font-size: 24px; }

